
.coupon-container{
    display: flex;
    width: 95vw;
    height: 16vh;
    background-color: #05386b;
    padding-right: var(--bs-gutter-x,.75rem);
    padding-left: var(--bs-gutter-x,.75rem);
    margin-right: auto;
    margin-left: auto;
    /* width: 100%; */
    font-family: "Market Sans",Arial,sans-serif;
    text-decoration: none;
    color: white;
    align-items: center;
    overflow: hidden;
}
.coupon-container img{
    width: 7vw;
    height: 7vw;
    margin-right: 0.5vw;
    border-radius: 20%;
    background-color: #05386b;
    
}
.text{
    color: #8ee4af;
    position: relative;
    top: 5px;
    /* line-height: 0.6; */
    
    
}
.coupon-container h4{
    font-weight: bold;
    background-color: #05386b;
    font-size: 2.2vw;
    
}
