.coupon-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 5vw;
    width: 15vw;
    margin-left: 15vw;
}
.coupon-btn a{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    font-size: 1.5vw;
    font-weight: bold;
    font-family: sans-serif;
    text-decoration: none;
    color: #8ee4af;
    border: 2px solid #EDF5E1;
    letter-spacing: 2px;
    text-align: center;
    position: relative;
    transition: all .35s;
  }
  
  .coupon-btn a span{
    position: relative;
    z-index: 2;
  }
  
  .coupon-btn  a:after{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: #EDF5E1;
    transition: all .35s;
  }
  
  .coupon-btn a:hover{
    color: #8ee4af;
  }
  
  .coupon-btn a:hover:after{
    width: 100%;
  }
  @media (max-width:600px)  {
    .coupon-container{
      display: flex;
      align-items: center;
      height: 7vh;
    }
    .coupon-container img{
      
      width: 10vw;
      height: 10vw;
      
  }
    .coupon-container h4{
      margin-bottom: 0;
      font-size: 3vw;
      
  }
  .coupon-container p{
    padding: 0;
    font-size: 1.5vw;
}
.coupon-btn{
  height: 7vw;
  width: 25vw;
  margin-left: 15vw;
}
.coupon-btn a{
  font-size: 2vw;
  border: 1px solid #EDF5E1;
}
  }
  @media (max-width:801px)  {
    .coupon-container{
      display: flex;
      align-items: center;
      height: 7vh;
    }
    .coupon-container img{
      
      width: 10vw;
      height: 10vw;
      
  }
    .coupon-container h4{
      margin-bottom: 0;
      font-size: 3vw;
      
  }
  .coupon-container p{
    padding: 0;
    padding-left: 2px;
    font-size: 1.5vw;
}
.coupon-btn{
  height: 7vw;
  width: 25vw;
  margin-left: 15vw;
}
.coupon-btn a{
  font-size: 2vw;
  border: 1px solid #EDF5E1;
}
  }